<!--
* @componmentName ScrollTable
* @module "src\PublicComponment\scrollTable"
* @fileDescription 用于多行无限滚动的表格组件，优化DOM渲染
* @author li
* @date 2020年8月18日
* @param {Object} [dataHeap：{ titleData:[],contentData:[]}] 表格头部数据信息和表格内容数据信息
* @param {Object} [tableStyle: {// 每条高度 itemSize: 50,// 可视区域高度 screenHeight: 760 }] 表格自定义样式信息
* @param {Boolean} [filterStyle] 开启表格筛选
* @example 调用示例
* <scroll-table :dataHeap="dataHeap">
*  <template slot="showCondition" slot-scope="slot">
*    // 插槽调用筛选条件信息
*   <p>{{slot.filterCondition}}</p>
*  </template>
*  <template slot="tableHeader" slot-scope="slot">
*   <p>{{slot.title}}</p>
*  </template>
*  <template slot="tableBox" slot-scope="slot">
*   <p >{{slot.value}}</p>
*  </template>
* </scroll-table>
 -->

<template>
  <div class="scrollTable">
    <header class="header-condition" v-if="isFilter">
      <!-- 展示筛选条件 -->
      <slot name="tableTitle"></slot>

      <div
        class="condition-tag"
        :style="{ background: '#5F7EC6', color: '#fff' }"
      >
        Filter condition:
      </div>
      <div
        class="anticon-tag-condition"
        v-for="(items, key) in filtersCondition"
        :key="key"
      >
        <div
          class="condition-tag"
          :style="{ background: colors[key], color: '#fff' }"
        >
          <a @click="removedAllTag(items)">
            <i class="anticon anticon-close">
              <svg
                viewBox="64 64 896 896"
                data-icon="close"
                width="1em"
                height="1em"
                fill="currentColor"
                aria-hidden="true"
                class
              >
                <path
                  d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 0 0 203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z"
