          :style="{ background: colors[index], color: '#fff' }"
        >
          <span v-if="typeof item === 'object'"
            >{{ item[0] }}~{{ item[1] }}</span
          >
          <span v-else>{{ item }}</span>
          <a @click="removedTag(items, item)">
            <i class="anticon anticon-close">
              <svg
                viewBox="64 64 896 896"
                data-icon="close"
                width="1em"
                height="1em"
                fill="currentColor"
                aria-hidden="true"
                class
              >
                <path
                  d="M563.8 512l262.5-312.9c4.4-5.2.7-13.1-6.1-13.1h-79.8c-4.7 0-9.2 2.1-12.3 5.7L511.6 449.8 295.1 191.7c-3-3.6-7.5-5.7-12.3-5.7H203c-6.8 0-10.5 7.9-6.1 13.1L459.4 512 196.9 824.9A7.95 7.95 0 0 0 203 838h79.8c4.7 0 9.2-2.1 12.3-5.7l216.5-258.1 216.5 258.1c3 3.6 7.5 5.7 12.3 5.7h79.8c6.8 0 10.5-7.9 6.1-13.1L563.8 512z"
                />
              </svg>
            </i>
          </a>
        </div>
        )
      </div>
      <slot name="showCondition" :filtersCondition="filtersCondition"></slot>
    </header>
    <section
      class="infinite-list-container"
      ref="list"
      @scroll="scrollEvent($event)"
      v-if="isNoData"
      :style="{ maxWidth: headerWidth + 'px' }"
    >
      <!-- 表格背景 -->
      <div
        class="infinite-list-phantom"
        :style="{ height: listHeight + 'px' }"
      ></div>

      <!-- 表格头部 -->
      <div
        class="infinite-list-header"
        :style="{ width: headerWidth + 'px', minWidth: headerWidth + 'px' }"
      >
        <div
          class="infinite-list-header-item"
          v-for="(items, key) in headerData"
          :key="key"
          :style="{ width: items.width + 'px', minWidth: items.width + 'px' }"
        >
          <slot name="tableHeader" :title="items.name"></slot>
          <div v-if="isFilter">
            <div
              v-if="items.filter"
              style="margin: 0.3125rem 0 0 0.3125rem; position: relative"
