                />
              </svg>
              <div class="filter-box" v-if="isKey == key">
                <div class="number-checkbox" v-if="items.type === 'checkbox'">
                  <div
                    class="number-list"
                    v-for="(item, index) in items.list"
                    :key="index"
                  >
                    <input
                      class="check-box"
