                      :value="item"
                      v-model="checkBoxArr"
                      @change="checkBox(items.name, item, items.type, key)"
                    />
                    <label for="runoob" :title="item">{{ item }}</label>
                  </div>
                </div>
                <div class="string-input" v-if="items.type === 'number'">
                  <input
                    type="text"
                    class="input-two-type"
                    v-model="textNumberMin"
                    :placeholder="items.min"
                  />~
                  <input
                    type="text"
                    class="input-two-type"
                    v-model="textNumberMax"
                    :placeholder="items.max"
                  />
                  <button
                    class="string-button"
                    @click="enterNumber(items.name, items.type, key)"
                  >
                    <svg
                      t="1600138204137"
                      class="icon"
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="6687"
                      width="20"
