                    >
                      <path
                        d="M605.1 280.6c-2.7-2.7-6.4-4-10.6-4.1h-0.4c-7 0.1-15.3 3.7-22 10.5-10.9 10.9-13.8 25.7-6.4 33l26.8 26.8-166.6 87.6-31.1-31.1c-7.4-7.3-22.2-4.5-33 6.4-10.9 10.9-13.8 25.7-6.4 33L581.8 669c7.3 7.4 22.1 4.5 33-6.4 10.9-10.9 13.8-25.7 6.4-33l-31-31.1 92.2-162 22.1 22.1c7.3 7.4 22.1 4.5 33-6.4 10.9-10.9 13.8-25.7 6.4-33L605.1 280.6zM467.3 584.9l-27.7-27.7-2.1-2.1c-2-1.6-4.4-2.6-7.2-2.6-2.5 0-4.8 0.8-6.5 2.2-0.9 0.9-1.9 1.8-2.9 2.8-25.2 24.3-93.9 94.1-141.9 175.4-0.9 1.5-1.7 2.9-2.6 4.4-0.1 0.6-0.4 1.2-0.4 1.8 0 4.6 3.7 8.3 8.3 8.3 1 0 1.9-0.2 2.8-0.6 1-0.7 2.1-1.3 3.1-2 78.3-49.5 149.1-115.2 175.2-140.5 1.7-1.7 3.2-3.1 4.6-4.5 1.4-1.8 1.8-3.7 1.8-5.9 0-2.8-1.1-5.2-2.6-7.2l-1.9-1.8z"
                        fill="#264A9C"
