                      />
                      <path
                        d="M511 64.2c-247.2 0-447.7 200.4-447.7 447.7S263.8 959.6 511 959.6s447.7-200.4 447.7-447.7S758.2 64.2 511 64.2zM785.1 786c-35.6 35.6-77.1 63.6-123.2 83.1-47.8 20.2-98.5 30.4-150.9 30.4s-103.1-10.2-150.9-30.4c-46.2-19.5-87.6-47.5-123.2-83.1-35.6-35.6-63.6-77.1-83.1-123.2-20.2-47.8-30.4-98.5-30.4-150.9s10.2-103.1 30.4-150.9c19.5-46.2 47.5-87.6 83.1-123.2 35.6-35.6 77.1-63.6 123.2-83.1 47.8-20.2 98.5-30.4 150.9-30.4s103.1 10.2 150.9 30.4c46.2 19.5 87.6 47.5 123.2 83.1 35.6 35.6 63.6 77.1 83.1 123.2 20.2 47.8 30.4 98.5 30.4 150.9S888.4 615 868.2 662.8c-19.5 46.1-47.5 87.6-83.1 123.2z"
                        fill="#264A9C"
                        p-id="6689"
                      />
                    </svg>
                  </button>
                </div>
                <div class="string-input" v-if="items.type === 'string'">
                  <select class="choice" @change="handleChange" value="请选择">
                    <option v-for="i in choice" :key="i.label">
                      {{ i.value }}
                    </option>
                  </select>
                  <input
                    type="text"
                    class="input-two-type"
                    v-model="textString"
                  />

                  <button
                    class="string-button"
                    @click="enterSubmit(items.name, items.type, key)"
                  >
                    <svg
                      t="1600138204137"
                      class="icon"
                      viewBox="0 0 1024 1024"
                      version="1.1"
                      xmlns="http://www.w3.org/2000/svg"
                      p-id="6687"
                      width="20"
                      height="20"
                    >
                      <path
                        d="M605.1 280.6c-2.7-2.7-6.4-4-10.6-4.1h-0.4c-7 0.1-15.3 3.7-22 10.5-10.9 10.9-13.8 25.7-6.4 33l26.8 26.8-166.6 87.6-31.1-31.1c-7.4-7.3-22.2-4.5-33 6.4-10.9 10.9-13.8 25.7-6.4 33L581.8 669c7.3 7.4 22.1 4.5 33-6.4 10.9-10.9 13.8-25.7 6.4-33l-31-31.1 92.2-162 22.1 22.1c7.3 7.4 22.1 4.5 33-6.4 10.9-10.9 13.8-25.7 6.4-33L605.1 280.6zM467.3 584.9l-27.7-27.7-2.1-2.1c-2-1.6-4.4-2.6-7.2-2.6-2.5 0-4.8 0.8-6.5 2.2-0.9 0.9-1.9 1.8-2.9 2.8-25.2 24.3-93.9 94.1-141.9 175.4-0.9 1.5-1.7 2.9-2.6 4.4-0.1 0.6-0.4 1.2-0.4 1.8 0 4.6 3.7 8.3 8.3 8.3 1 0 1.9-0.2 2.8-0.6 1-0.7 2.1-1.3 3.1-2 78.3-49.5 149.1-115.2 175.2-140.5 1.7-1.7 3.2-3.1 4.6-4.5 1.4-1.8 1.8-3.7 1.8-5.9 0-2.8-1.1-5.2-2.6-7.2l-1.9-1.8z"
                        fill="#264A9C"
                        p-id="6688"
                      />
                      <path
                        d="M511 64.2c-247.2 0-447.7 200.4-447.7 447.7S263.8 959.6 511 959.6s447.7-200.4 447.7-447.7S758.2 64.2 511 64.2zM785.1 786c-35.6 35.6-77.1 63.6-123.2 83.1-47.8 20.2-98.5 30.4-150.9 30.4s-103.1-10.2-150.9-30.4c-46.2-19.5-87.6-47.5-123.2-83.1-35.6-35.6-63.6-77.1-83.1-123.2-20.2-47.8-30.4-98.5-30.4-150.9s10.2-103.1 30.4-150.9c19.5-46.2 47.5-87.6 83.1-123.2 35.6-35.6 77.1-63.6 123.2-83.1 47.8-20.2 98.5-30.4 150.9-30.4s103.1 10.2 150.9 30.4c46.2 19.5 87.6 47.5 123.2 83.1 35.6 35.6 63.6 77.1 83.1 123.2 20.2 47.8 30.4 98.5 30.4 150.9S888.4 615 868.2 662.8c-19.5 46.1-47.5 87.6-83.1 123.2z"
                        fill="#264A9C"
                        p-id="6689"
                      />
                    </svg>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- 表格内容 -->
      <div class="infinite-list" :style="{ transform: getTransform }">
        <!-- 表格展示修饰 -->
        <div
          ref="items"
          class="infinite-list-item"
          v-for="items in visibleData"
          :key="items.id"
          :style="{ width: headerWidth + 'px', minWidth: headerWidth + 'px' }"
        >
          <div
            v-for="(value, key, index) in items"
            :key="index"
            class="table-box"
            :style="{
              height: itemSize + 'px',
              lineHeight: itemSize - 20 + 'px',
              width: headerData[key].width + 'px',
              minWidth: headerData[key].width + 'px',
            }"
          >
            <div v-if="visibleData.length < 1">
              <div class="infinite-no-data">No data</div>
            </div>
            <div v-else>
              <slot name="tableBox" :value="value"></slot>
            </div>
          </div>
        </div>
      </div>
    </section>

    <section v-else class="no-data">NO DATA</section>
  </div>
</template>


<script>
import clickoutside from "./clickoutside.js";
// 颜色数组
import colors from "./colors";
const choice = [
  {
    value: "exact",
    label: "exact",
  },
  {
    value: "contain",
    label: "contain",
  },
];
export default {
  name: "scrollTable",

  props: {
    dataHeap: {
      type: [Object, Array],
    },
    filterStyle: {
      type: Boolean,
    },
    tableStyle: {
      type: Object,
    },
  },

  data() {
    return {
      choice,
      condition: "exact",
      // 内容数据
      data: [],
      // 头部数据
      headerData: [],
      // 是否开启筛选展示
      isFilter: true,
      // 是否开启表格显示
      isNoData: true,
      // 每条高度
      itemSize: 50,
      // 可视区域高度
      screenHeight: 0,
      // 偏移量
      startOffset: 0,
      // 起始索引
      start: 0,
      // 结束索引
      end: null,
      // 控制筛选box显示与否
      isShow: false,
      // 判断单个box的显示
      isKey: null,
      // 当前头部类型
      type: "",
      // 暂存筛选条件
      filtersCondition: [],
      // 颜色列表
      colors: [],
      // 储存checkbox数据
      checkBoxArr: [],
      //数字输入框
      textNumberMax: "",
      textNumberMin: "",
      //string输入框
      textString: "",
    };
  },

  computed: {
    //列表总高度
    listHeight() {
      return this.data.length * this.itemSize;
    },
    //可显示的列表项数
    visibleCount() {
      return Math.ceil(this.screenHeight / this.itemSize);
    },
    //偏移量对应的style
    getTransform() {
      return `translate3d(0,${this.startOffset}px,0)`;
    },
    //获取真实显示列表数据
    visibleData() {
      return this.data.slice(this.start, Math.min(this.end, this.data.length));
    },
    //头部的每一块的宽度
    headerWidth() {
      let num = 0;
      this.headerData.forEach((element) => {
        num += element.width;
      });
      return num;
    },
  },

  // 自定义指令
  directives: {
    clickoutside,
  },

  created() {
    // 获取颜色
    this.colors = colors.colors;
    // 判断时候有传递数据
    if (JSON.stringify(this.dataHeap) === "{}") {
      // 更改显示
      this.isNoData = false;
      this.isFilter = false;
    } else {
      // 重新赋值prop的数据
      this.data = [...this.dataHeap.contentData];
      this.headerData = [...this.dataHeap.titleData];
    }
    if (this.filterStyle === false) {
      this.isFilter = false;
    } else {
      this.isFilter = true;
    }
  },

  mounted() {
    // 页面数据初始化
    this.screenHeight = this.$el.clientHeight;
    this.start = 0;
    // 这里的截取结束位置需要根据开始位置和首屏显示的条数来确定
    this.end = this.start + this.visibleCount;
    this.$forceUpdate();
  },

  methods: {
    handleChange(e) {
      this.condition = e.target.value;
    },
    // 滚动事件
    scrollEvent() {
      // 当前滚动位置
      let scrollTop = this.$refs.list.scrollTop;
      // 此时的开始索引
      this.start = Math.floor(scrollTop / this.itemSize);
      // 此时的结束索引
      this.end = this.start + this.visibleCount;
      // 滚动时列表盒子的偏移量
      this.startOffset = scrollTop - (scrollTop % this.itemSize);
    },

    // 开启筛选框的点击事件
    handleShow(k, n, t) {
      // 当前key
      this.isKey = k;
      // 开启显示
      this.$nextTick(() => {
        this.isShow = true;
      });
      // header name
      this.field = n;
      // 当前类型
      this.type = t;
    },

    //复选框逻辑 参数n是列名，j是list元素，t是当前数据的类型 , k当前列的key
    checkBox(n, j, t, k) {
      // 点击当前的条件（j），先判断是否在this.checkBoxArr中存在该条件
      if (this.checkBoxArr.length === 0) {
        // 若是存在，则需要删除
        // 找到筛选条件（this.filtersCondition）中的位置
        let index = this.filtersCondition.findIndex(
          (element) => element.name === n
        );
        this.filtersCondition.splice(index, 1);
      } else {
        // 不存在则存在两种添加方式，首次添加则需要将整个大条件push到this.filtersCondition中
        if (
          this.filtersCondition.findIndex((element) => element.name === n) ===
          -1
        ) {
          this.filtersCondition.push({
            name: n,
            key: k,
            condition: [j],
            type: t,
          });
        } else {
          // 多次添加则需要在condition中push this.checkBoxArr的数据
          this.filtersCondition.find((ele) => ele.key === k).condition = [
            ...this.checkBoxArr,
          ];
        }
      }
      // 开启查找函数
      this.data = [...this.searchValue()];
    },

    // string输入框  参数n是列名，t是当前数据的类型 , k是当前列的下标
    enterSubmit(n, t, k) {
      // 判断是否存在该条件
      // 添加计算机读取条件
      if (this.filtersCondition.some((res) => res.key === k)) {
        this.filtersCondition
          .find((ele) => ele.key === k)
          .condition.push(this.textString);
      } else {
        this.filtersCondition.push({
          name: n,
          key: k,
          condition: [this.textString],
          type: t,
          choice: this.condition,
        });
      }
      // 开启查找函数
      this.data = [...this.searchValue()];
      this.condition = "exact";
    },

    // number输入框  参数n是列名，t是当前数据的类型 , k是当前列的下标
    enterNumber(n, t, k) {
      // 添加计算机读取条件
      let arr = [this.textNumberMin, this.textNumberMax];
      // let obj = {ge:this.textNumberMin,le: this.textNumberMax};
      if (this.filtersCondition.some((res) => res.key === k)) {
        this.filtersCondition.find((ele) => ele.key === k).condition.push(arr);
      } else {
        this.filtersCondition.push({
          name: n,
          key: k,
          condition: [arr],
          type: t,
        });
      }
      // 开启查找函数
      this.data = [...this.searchValue()];
    },

    // 提供checkout的筛选，字符串的模糊搜索，以及数字的范围筛选, 筛选逻辑为同列是或的关系，不同列则是与的关系。
    searchValue() {
      let list = [];
      // 获取到原始数组的信息（不能直接更改原始的数据）
      let listStorage = [...this.dataHeap.contentData];

      if (this.filtersCondition.length == 0) {
        return [...this.dataHeap.contentData];
      }
      if (this.condition == "exact") {
        // 开始循环原始数组
        listStorage.forEach((items) => {
          // 判断数据是否符合条件
          if (
            // 先判断不同列的与的关系，剔除不需要的
            this.filtersCondition.every((ele) => {
              // 判断类型
              if (ele.type === "string" || ele.type === "checkbox") {
                // 同列进行与的关系判断
                return ele.condition.some(
                  (item) => items[Number(ele.key)].toString() == item
                );
              } else if (ele.type === "number") {
                // 同列进行与的关系判断
                return ele.condition.some(
                  (item) =>
                    !!(
                      Number(item[0]) <= Number(items[Number(ele.key)]) &&
                      Number(items[Number(ele.key)]) <= Number(item[1])
                    )
                );
              }
            })
          ) {
            // 符合就push到临时数组中
            list.push(items);
          }
        });
        return list;
      } else {
        // 开始循环原始数组
        listStorage.forEach((items) => {
          // 判断数据是否符合条件
          if (
            // 先判断不同列的与的关系，剔除不需要的
            this.filtersCondition.every((ele) => {
              // 判断类型
              if (ele.type === "string" || ele.type === "checkbox") {
                // 同列进行与的关系判断
                return ele.condition.some(
                  (item) =>
                    !!(items[Number(ele.key)].toString().indexOf(item) > -1)
                );
              } else if (ele.type === "number") {
                // 同列进行与的关系判断
                return ele.condition.some(
                  (item) =>
                    !!(
                      Number(item[0]) <= Number(items[Number(ele.key)]) &&
                      Number(items[Number(ele.key)]) <= Number(item[1])
                    )
                );
              }
            })
          ) {
            // 符合就push到临时数组中
            list.push(items);
          }
        });
        return list;
      }
    },

    // removedAllTag 移除当列所有筛选条件，n 为当前列的name
    removedAllTag(n) {
      // 删除计算机读取条件
      let index = this.filtersCondition.indexOf(n);
      this.filtersCondition.splice(index, 1);
      // 开启查找函数
      this.data = [...this.searchValue()];
    },

    removedTag(n, c) {
      let index = this.filtersCondition.indexOf(n);
      if (this.filtersCondition[index].condition.length == 1) {
        this.filtersCondition.splice(index, 1);
      } else {
        this.filtersCondition[index].condition.splice(
          this.filtersCondition[index].condition.indexOf(c),
          1
        );
      }
      // 开启查找函数
      this.data = [...this.searchValue()];
    },
  },

  destroyed() {
    this.data = [];
  },

  watch: {
    dataHeap() {
      // 重新赋值prop的数据
      this.data = [...this.dataHeap.contentData];
      this.headerData = [...this.dataHeap.titleData];
    },

    isKey() {
      // 数组置空,只要发生变化就需要将临时check value置空。
      this.checkBoxArr = [];
      // 开启验证
      if (this.type == "checkbox") {
        // 循环数组开始查找
        this.headerData.forEach((ele) => {
          // 找到与当前触发的对应数据
          if (ele.name == this.field) {
            // 循环list，进行数据修正
            ele.list.forEach((ele_) => {
              // 若存在条件，则状态需要为选中状态
              if (
                this.filtersCondition.findIndex(
                  (element) => element.name === ele_
                ) >= 0
              ) {
                // 需要往checkBoxArr中push
                this.checkBoxArr.push(ele_);
              }
            });
          }
        });
      } else if (this.type == "string") {
        this.textString = "";
      } else {
        this.textNumberMax = "";
        this.textNumberMin = "";
      }
    },

    isShow() {
      if (!this.isShow) {
        this.isKey = null;
      }
    },
  },
};
</script>
 
